import React from 'react';
import {Container, Col, Row} from "react-bootstrap"



const thesisurl = "https://etd.ohiolink.edu/apexprod/rws_olink/r/1501/10?clear=10&p10_accession_num=osu1224705112";

export default function Publications () {

  return (
    <>
      <h1>Academic Publications</h1>
      <Container>
            <Row md={2} xs={1} lg={3} className="g-3">
                
            <Col>
            <a href={thesisurl}>
            <img src="/imgs/blade.jpg" alt="Image 1" />
            </a>
            <p>MS Thesis pdf</p>
            </Col>

            <Col>
            <a href="https://www.example.com/image2">
            <img src="image2.jpg" alt="Image 2" />
            </a>
            <p>Caption for Image 2</p>
            </Col>
        
      </Row>
    </Container>
      </>
  )
}
